@-webkit-keyframes icon {
  0% {
    opacity: 0;
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  50% {
    opacity: 1;
    -webkit-transform: scale(1.3) rotate(-2deg);
    transform: scale(1.3) rotate(-2deg);
  }
  100% {
